import { HttpClient } from '@angular/common/http';
import { Component, OnInit, HostListener } from '@angular/core';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import { UserIdleService } from 'angular-user-idle';
import { PouchDBService } from './services/pouchdb.service';
import { AuthService } from './services/auth.service';
import { SessionService } from './services/session.service';


interface TokenResponse {
  token: string;
}
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit {
  title = 'monitor';
  dbServices: any;
  private token: string;
  timeStart = false;
  seconds = 15;
  localItem: any = {};
  clientX = 0;
  clientY = 0;
  sessionData: any = {};

  constructor(private http: HttpClient, private router: Router, private userIdle: UserIdleService, public pouchDBService: PouchDBService,
    private _authService: AuthService,
    private sessionService: SessionService) {
    this.dbServices = environment.API_URL;

  }

  ngOnInit(): void {
    this.localItem = this._authService.decode();
    console.log(this.localItem);
    console.log("inicia app:", new Date().toString());
    this.userIdle.startWatching();

    // Start watching when user idle is starting.
    this.userIdle.onTimerStart().subscribe(count => {
      (count == 1) ? console.log("onTimerStart:", new Date().toString()) : '';
      console.log("count :", count);
    });

    // Start watch when time is up.
    this.userIdle.onTimeout().subscribe(() => {

      console.log("onTimeOut");
      this.sessionData = {
        name: this.localItem.id,
        isActive: "0"
      }
      this.sessionService.update(this.sessionData).subscribe(result => {
        this.router.navigate(['/register']);
      },
        (error) => {
          console.log('error al actualizar la sesion: ' + error);
        });
    });
    this.save().subscribe(
      () => {
        
     
      },
      err => {
        console.info(err);
      }) 
  }

  public save(): Observable<any> {
    const url = this.dbServices + '/getToken';
    const base = this.http.get(url);

    const request = base.pipe(
      map((data: TokenResponse) => {
        if (data.token) {
          this.saveToken(data.token);
        }
        return data
      })
    )

    return request
  }

  private saveToken(token: string): void {
    if (localStorage.getItem('tokenServicios') === null) {
      localStorage.setItem('tokenServicios', token)
      this.token = token
    }
  }

  stop() {
    console.log("stopTimer", new Date().toString());
    this.userIdle.stopTimer();
  }


  stopWatching() {
    this.userIdle.stopWatching();
  }

  startWatching() {
    console.log("startWatching: (1320s)", new Date().toString());
    this.userIdle.startWatching();
  }

  restart() {
    this.userIdle.resetTimer();
  }


  coordinates(event: MouseEvent): void {
    this.clientX = event.clientX;
    this.clientY = event.clientY;

  }

  // @HostListener('mousemove') onMove() {
  //   window.alert("mouse is moved");
  // } 
  @HostListener('click') onClick() {
    console.log("click");
    this.stop();
    this.restart();
  }

  @HostListener('keypress') onKeyPress() {
    console.log("keypress");
    this.stop();
    this.restart();
  }
}
