  update() {
    this.chartOptionFlow_signal = this.chartData.Flow_signal(this.jsonData.series_data.flow_signal);
    this.chartOptionFlow_signal.yAxis = {
      max: 60
    }
    this.chartOptionPress_signal = this.chartData.Press_signal(this.jsonData.series_data.press_signal);

    if (this.jsonData.metrics.exp_H1DC > 0) {
      this.jsonData.metrics.exp_H1DC = Number.parseFloat(this.jsonData.metrics.exp_H1DC).toFixed();
      this.valStyle = this.chartData.style(this.jsonData.metrics.exp_H1DC);
      this.H1DC.push(this.jsonData.metrics.exp_H1DC);

    } else {
      this.H1DC.push(0);
      this.valStyle = this.chartData.style(0);
      this.jsonData.metrics.exp_H1DC = 0;
    }


  }

  chartOptionFlow_signal: EChartOption = {};
  chartOptionPress_signal: EChartOption = {}


  mode(val) {
    let numMode
    let mode_list = ['N/A', 'STBY', 'Press Control', 'Vol Control', 'PRVC', 'Vol Support',
      'SIMV Vol', 'SIMV Press', 'PS CPAP', 'N/A', 'SIMV PRVC', 'Bivent',
      'PS NIV', 'PS-CPAP-NIV', 'Nasal CPAP', 'NAVA', 'PRVC Auto', 'N/A',
      'PS AutoMode', 'VC AutoMode', 'PRVC Auto', 'PS CPAPA Auto',
      'VS switch', 'VS switch']
    if (!val) {
      numMode = 0;
    } else {
      numMode = parseFloat(val);
    }

    return mode_list[numMode];

  }

  valNum(val) {
    if (val === '9999' || val === '') {
      return 'N/A';
    } else {
      return parseFloat(val).toFixed();
    }
  }




