  public getEpoca(){
    this.blockUI.start('Loading...');
    this.pouchDBService.getEpocaRandom().subscribe(result => {
      this.supervision = new Supervision();
      setTimeout(() => {
        this.blockUI.stop(); // Stop blocking
      }, 1000);
      //if(result.docs[0]){
        if(result){
        this.result_gral = {...result} 
        console.log('copia clone ... ', this.result_gral)
        this.flagData = true;
        //this.jsonData = result.docs[0];
        this.jsonData = result[this.index].doc;
        this.update();
        this.validateData(this.jsonData);
        if (this.jsonData.demographics.gender === 1) {
          this.genero = 'masculino'
        } else if (this.jsonData.demographics.gender === 0) {
          this.genero = 'femenino';
        }
        console.log(this.jsonData);
        const d = new Date(this.jsonData.time_variables.date_created_utc + ' UTC');
        console.log(d.toString());
        this.demographics = {
          MRN: this.jsonData.MRNa,
          Gender: this.jsonData.demographics.gender === 1 ? 'Male' : 'Female',
          Age: `${this.jsonData.demographics.age} years old`,
          Dx1: this.jsonData.demographics.diagnosis,
          Covid: this.jsonData.demographics.covid_19 ? 'Yes': 'No',
          SAPS: this.jsonData.demographics.saps,
          SOFA: this.jsonData.demographics.sofa,
          Update: d,
        };
                
        if (typeof this.jsonData.recommendations != "undefined") {          
          this.assessment = this.jsonData.recommendations.assessment;
            if (typeof this.jsonData.recommendations.comment_out!= "undefined") {
                this.recommendations = this.jsonData.recommendations.comment_out.filter(item => item !== ''); }
        }


      }else{
        this.flagData = false;
      }
   }); 
  }

