import { Component, OnInit, HostListener } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ChartService } from 'src/app/services/chart.service';
import { NgbModal, ModalDismissReasons, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { PouchDBService } from '../../services/pouchdb.service';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';
import Swal from 'sweetalert2';
import { HospitalesService } from 'src/app/services/hospitales.service';


@Component({
  selector: 'app-hospitales',
  templateUrl: './hospitales.component.html',
  styleUrls: ['./hospitales.component.css']
})

export class HospitalesComponent implements OnInit {
  idioma: string;
  registerForm: FormGroup;

  user: any = {};
  userObject: any = {};
  selectedVariables: any = {};
  selectedVariablesNo: any = {};

  listaUsuarios: any = [];
  ltsAsignados: any = [];
  ltsNoAsignados: any = [];

  lstHospitales: any = [];
  pageActual = 1;
  pageNumItems = 5;
  lstHospitalesFilt: any = [];
  readModal: number;
  readConditions = false;

  unamePattern = "^[a-zA-Z0-9\-\_\.]+$";
  hospitalPattern = "^[a-zA-Z\ \]+$";
  submitted = false;
  closeResult: string;
  modalOptions: NgbModalOptions;
  localItem: any = {};
  consecutivo: string = "";
  cambioHospital = false;
  hospital: any = {};

  constructor(public translate: TranslateService,
    private formBuilder: FormBuilder,
    public chartService: ChartService,
    public pouchDBService: PouchDBService,
    private modalService: NgbModal,
    private toastr: ToastrService,
    private _authService: AuthService,
    private hospitalesService: HospitalesService) {
    this.modalOptions = {
      backdrop: 'static',
      backdropClass: 'customBackdrop',
      size: 'lg'
    }
    translate.addLangs(['en', 'es']);
    translate.setDefaultLang('en');
    this.idioma = this.translate.currentLang;
  }

  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      country: ['', [Validators.required]],
      hospital: ['', [Validators.minLength(5), Validators.required]]
    });
    this.localItem = this._authService.decode();
    this.user.country = '';
    this.obtenerHospitales();
  }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.registerForm.invalid ) {
      return;
    }
    this.formatUser();
    this.hospitalesService.insert(this.userObject).subscribe(result => {
      if (!result.error) {
        this.obtenerHospitales();
        this.limpiar();
        this.toastr.success(this.translate.instant('hospitalInsertado'));
      } else {
        this.toastr.info(this.translate.instant('hospitalExistente'));
      }
    });
  }

  onReset() {
    this.submitted = false;
    this.registerForm.reset();
  }

  public formatUser() {
    this.consecutivo = this.lstHospitales.length + 1;
    this.userObject._id = this.consecutivo.toString();
    this.userObject.country = this.user.country.toUpperCase();
    this.userObject.hospital = this.user.hospital.toUpperCase();
    this.userObject.usuario = this.localItem.id.toUpperCase();
    this.userObject.fecha_creacion = new Date().toLocaleDateString();
    this.userObject.activo = true;
  }

  obtenerHospitales() {
    this.hospitalesService.findAll().subscribe((rsUsuarios) => {
      this.lstHospitales = rsUsuarios;
      console.log(this.lstHospitales);
    });



  }
  get f() { return this.registerForm.controls; }

  public read() {
    if (this.readModal === 3) {
      this.userObject.termsConditions = true;
    }
    this.cancel();
  }

  cancel() {
    this.modalService.dismissAll();
    this.readConditions = false;

  }

  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    if ((event.target.scrollTop || event.body.scrollTop) + event.target.offsetHeight + 30 >= event.target.scrollHeight) {
      this.readConditions = true;

    }
  }

  /* funcion que abre modal generico */
  open(content, value) {
    this.modalService.open(content, this.modalOptions).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  /* funcion para detectar acciones con teclado  */
  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  public limpiar() {
    this.onReset();
    this.cancel();
    this.userObject.termsConditions = false;
    this.registerForm = this.formBuilder.group({
      country: ['', [Validators.required]],
      hospital: ['', [Validators.minLength(5), Validators.required]]
    });
    this.user.country = '';

  }

  editar(value) {
    let option = '';
    let paisSeleccionado = '';
    for (let i = 0; i <= 9; i++) {
      if (i === 0) {
        if (value.country !== 'ARGENTINA') {
          option += '<option value="ARGENTINA">Argentina</option>';
        } else {
          paisSeleccionado = 'Argentina';
        }
      }
      if (i === 1) {
        if (value.country !== 'BRASIL') {
          option += '<option value="BRASIL">Brasil</option>';
        } else {
          paisSeleccionado = 'Brasil';
        }
      }
      if (i === 2) {
        if (value.country !== 'COLOMBIA') {
          option += '<option value="COLOMBIA">Colombia</option>';
        } else {
          paisSeleccionado = 'Colombia';
        }
      }
      if (i === 3) {
        if (value.country !== 'COSTA_RICA') {
          option += '<option value="COSTA_RICA">Costa rica</option>';
        } else {
          paisSeleccionado = 'Costa rica';
        }
      }
      if (i === 4) {
        if (value.country !== 'ECUADOR') {
          option += '<option value="ECUADOR">Ecuador</option>';
        } else {
          paisSeleccionado = 'Ecuador';
        }
      }
      if (i === 5) {
        if (value.country !== 'EUA') {
          option += '<option value="EUA">Estados Unidos</option>';
        } else {
          paisSeleccionado = 'Estados Unidos';
        }
      }
      if (i === 6) {
        if (value.country !== 'MEXICO') {
          option += '<option value="MEXICO">Mxico</option>';
        } else {
          paisSeleccionado = 'Mxico';
        }
      }
      if (i === 7) {
        if (value.country !== 'PANAMA') {
          option += '<option value="PANAMA">Panam</option>';
        } else {
          paisSeleccionado = 'Panam';
        }
      }
      if (i === 8) {
        if (value.country !== 'PERU') {
          option += '<option value="PERU">Per</option>';
        } else {
          paisSeleccionado = 'Per';
        }
      }
      if (i === 9) {
        if (value.country !== 'REPUBLICA_DOMINICANA') {
          option += '<option value="REPUBLICA_DOMINICANA">Repblica Dominicana</option>';
        } else {
          paisSeleccionado = 'Repblica Dominicana';
        }
      }
    }
    let optionEstatus = '';
    let estatusSeleccionado = '';
    let valorEstatus = value.activo
    for (let j = 0; j <= 1; j++) {
      if (j === 0) {
        if (!!value.activo) {
          optionEstatus += '<option value="0">Inactivo</option>';
          estatusSeleccionado = 'Activo';
        }
      }
      if (j === 1) {
        if (!!!value.activo) {
          optionEstatus += '<option value="1">Activo</option>';
          estatusSeleccionado = 'Inactivo';
        }
      }
    }
    Swal.fire({
      title: this.translate.instant('editarTooltip'),
      html: '<input placeholder="&#x269a; Hospital" type="text" value="' + value.nombre + '"class="swal2-input" id="hospitalModal" name="hospitalModal" style="padding: 10px;font-family: FontAwesome, &quot;Open Sans&quot;, Verdana, sans-serif;font-style: normal;font-weight: normal;text-decoration: inherit;"><select class="swal2-select" id="pais" style="display: flex;border-radius: 5px;width: 5.5em;float: left;"><option value="' + value.country + '">' + paisSeleccionado + '</option>' + option + '</select><select class="swal2-select" id="estatus" style="display: flex;border-radius: 5px;"><option value=' + valorEstatus + '>' + estatusSeleccionado + '</option>' + optionEstatus + '</select>',
      focusConfirm: false,
      showCancelButton: true,
      confirmButtonColor: '#17a2b8',
      cancelButtonColor: '#d33',
      confirmButtonText: this.translate.instant('confirmarContrasena'),
      cancelButtonText: this.translate.instant('cancelar'),
      preConfirm: () => {
        var hospital = (document.getElementById('hospitalModal') as HTMLInputElement).value.toUpperCase();
        var pais = (document.getElementById('pais') as HTMLInputElement).value.toUpperCase();
        var estatus = (document.getElementById('estatus') as HTMLInputElement).value;
        var activo: number = +estatus;

        if (hospital.length === 0 || pais.length === 0 || estatus.length === 0) {
          Swal.showValidationMessage(this.translate.instant('mensajeModal1'));
          return;
        }
        if (hospital.length < 5) {
          Swal.showValidationMessage(this.translate.instant('mensajeModal2'));
          return;
        }
        if(hospital !== value.nombre) {
          this.cambioHospital = true;
        }
        this.hospital = {
          cambioHospital: this.cambioHospital,
          hospital: hospital,
          country: pais,
          activo: activo,
          id: value.id,
        }
        this.hospitalesService.update(this.hospital).subscribe(result => {
          if (!result.error) {
            Swal.fire({
              text: this.translate.instant('hospitalActualizado'),
              icon: 'success',
              confirmButtonColor: '#17a2b8',
              onClose: () => {
                this.obtenerHospitales();
              }
            });
          } else {
            this.toastr.info(this.translate.instant('hospitalExistente'));
          }
        });
      }
    })
  }

  omit_special_char(event, type) {
    var regex;

    switch (type) {
      case 1:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        break;
      case 2:
        regex = new RegExp("^[a-zA-Z\ \]+$");
        break;
      case 3:
        regex = new RegExp("^[a-zA-Z0-9\.\_\@\-\]+$");
        break;
      default:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        break;
    }

    var key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
    if (!regex.test(key)) {
       event.preventDefault();
       return false;
    } 
  }

}