import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { ToastrService } from 'ngx-toastr';
import { SendgridService } from 'ngx-sendgrid';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { UserIdleService } from 'angular-user-idle';


@Injectable({
  providedIn: 'root'
})

export class EmailService {
  URL: string;


  constructor(
    private http: HttpClient,
    private toastr: ToastrService,
    private service: SendgridService,
    public authService: AuthService,
    private userIdle: UserIdleService
  ) {
    this.URL = environment.EMAIL;
    this.userIdle.resetTimer();

  }


  public sendCredential(object): Observable<any> {
    let url = this.URL + 'send-key';
    return this.http.post<any>(url, object);
  }


  public sendGenericEmail(email): Observable<any> {
    let url = environment.EMAIL + 'send';
    return this.http.post<any>(url, email);
  }


  getSendgridHttpHeaders() {

    this.checkSendgridTokenServices();
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'access-token': localStorage.getItem('tokenServiciosSendgrid')
      })
    };
    return httpOptions;
  }

  checkSendgridTokenServices() {
    const TWENTY_MIN = 20 * 60 * 1000;
    const currentDate = new Date();
    let recargarToken = false;


    if (localStorage.getItem('tokenServiciosSendgrid') != null) {
      const dateSent = new Date(localStorage.getItem('tokenTimeSendgrid'));
      console.log('dateSend' + dateSent + ' current ' + currentDate);
      let diffMs = currentDate.getTime() - dateSent.getTime();
      if (diffMs > TWENTY_MIN) {
        console.log('Delayed by more than 20 mins');
        recargarToken = true;
      }

    } else {
      recargarToken = true;
    }
    if (recargarToken) {

      console.log('Hay que recargar token');
      return this.obtenerTokenSendgrid().subscribe(result => {
        localStorage.removeItem('tokenServiciosSendgrid');
        localStorage.removeItem('tokenTimeSendgrid');
        localStorage.setItem('tokenServiciosSendgrid', result.token);
        localStorage.setItem('tokenTimeSendgrid', new Date().toUTCString());
      });
    }

  }

  public obtenerTokenSendgrid() {
    const url = this.URL + 'getToken';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

}
