import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router'
import { APP_ROUTES } from './app-routing.module';
import { AppComponent } from './app.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {NgbPaginationModule, NgbAlertModule} from '@ng-bootstrap/ng-bootstrap';
import { UserComponent } from './user/user.component';
import { PouchDBService } from './services/pouchdb.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CustomvalidationService } from './services/customvalidation.service';
import { ToastrModule } from 'ngx-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import {TranslateModule, TranslateLoader} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import { EmailService } from './services/email.service';
import { SendgridModule } from 'ngx-sendgrid';
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, "./assets/i18n/", ".json");
}
import { environment } from '../environments/environment';
import { AuthService } from './services/auth.service';
import { MonitorModule } from './monitor/monitor.module';
import {CommonModule }  from '@angular/common/';
import { DateRangePickerModule } from '@syncfusion/ej2-angular-calendars';
import { UserIdleModule } from 'angular-user-idle';

@NgModule({
  declarations: [
    AppComponent,
    UserComponent
    // ColumnaFechaPipe
  ],
  imports: [
    BrowserModule,
    NgbModule,
    NgbPaginationModule,
    NgbAlertModule,
    RouterModule.forRoot(APP_ROUTES),
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule, // required animations module
    ToastrModule.forRoot(), // ToastrModule added
    HttpClientModule,
    MonitorModule,
    CommonModule,
    DateRangePickerModule,
    TranslateModule.forRoot({
      loader: {
          provide: TranslateLoader,
          useFactory: HttpLoaderFactory,
          deps: [HttpClient]
      }
  }),
  SendgridModule.forRoot({token: environment.EMAIL_GRID_API}),
  UserIdleModule.forRoot({idle: 1320, timeout: 10, ping: 1})
],
  providers: [PouchDBService, CustomvalidationService, EmailService, AuthService],
  bootstrap: [AppComponent]
})
export class AppModule { }
