import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-recommendations',
  templateUrl: './recommendations.component.html',
  styleUrls: ['./recommendations.component.css']
})
export class RecommendationsComponent implements OnInit {

  Object = Object; 
  id_name_textarea:string="";

  @Input() info: any = {}
  @Input() name: string
  @Input() type: string

  constructor() { }

  ngOnInit(): void {
  }

  onChangeChBox(event) {
    this.id_name_textarea= "recommendation-ta-" + event.target.id.substr(15,1);    
    if(event.target.checked===false) {          
      (<HTMLInputElement>document.getElementById(this.id_name_textarea)).readOnly = false;      
    }
    if(event.target.checked===true) {          
      (<HTMLInputElement>document.getElementById(this.id_name_textarea)).readOnly = true;      
    }

  }

}


