import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AuthService } from './auth.service';

@Injectable({
    providedIn: 'root'
})

export class PacientesService {
    dbServices: string;
    apiUrl: string;
    urlPacientes: string;
    urlFindAll: string;
    urlFindByHospital: string;
    urlFindByUser: string;

    constructor(private http: HttpClient, private authService: AuthService) {
        this.dbServices = environment.DB_SERVICES;
        this.apiUrl   = environment.API_URL;
        this.urlPacientes = environment.URI_PACIENTES;
        this.urlFindAll = this.apiUrl + this.urlPacientes + environment.URI_FIND_ALL;
        this.urlFindByHospital = this.apiUrl + this.urlPacientes + environment.URI_FIND_BY_HOSPITAL;
        this.urlFindByUser = this.apiUrl + this.urlPacientes + environment.URI_FIND_BY_USER;
    }

    public findAll(): Observable<any> {
        return this.http.get(this.urlFindAll, this.authService.getHttpHeaders());
    }

    public findByHospital(hospital): Observable<any> {
        return this.http.get(this.urlFindByHospital + '/' + hospital, this.authService.getHttpHeaders());
    }

    public findByUser(user): Observable<any> {
        return this.http.get(this.urlFindByUser + '/' + user, this.authService.getHttpHeaders());
    }


    public getPatientsSuperAdmin() {
        const url = this.dbServices + '/searchPatients';
        return this.http.get(url).pipe(map(json => {
          const result = json;
          return result as any[];
        }));    
      }
    
      public getAllPatientsAdmHospital(hospital) {    
        const url = this.dbServices + '/searchPatientsAdminHospitalAll?hospital=' + hospital;
        return this.http.get(url).pipe(map(json => {
          const result = json;
          return result as any[];
        }));    
      }
    
      public getPatientsByUser(idUser) {    
        const url = this.dbServices + '/searchPatient?user=' + idUser;
        return this.http.get(url).pipe(map(json => {
          const result = json;
          return result as any[];
        }));
    
      }


}