  public getEpoca(idPatient, idEpoch) {
    this.userSelected = true;
    this.blockUI.start('Loading...');
    if (idEpoch !== '') {
      this.pouchDBService.getEpochFromPatientById(idEpoch).subscribe((result) => {
        this.supervision = new Supervision();
        setTimeout(() => {
          this.blockUI.stop(); 
        }, 1000);

        if (result) {
          this.flagData = true;
          this.jsonData = result;
          const d = new Date(this.jsonData.time_variables.date_created_utc + ' UTC');
          this.demographics = {
            MRN: this.jsonData.MRNa,
            Gender: this.jsonData.demographics.gender === 1 ? 'Male' : 'Female',
            Age: `${this.jsonData.demographics.age} years old`,
            Dx1: this.jsonData.demographics.diagnosis,
            Covid: this.jsonData.demographics.covid_19 ? 'Yes' : 'No',
            SAPS: this.jsonData.demographics.saps,
            SOFA: this.jsonData.demographics.sofa,
            Update: d,
          };
          if (typeof this.jsonData.recommendations != "undefined") {
            this.assessment = this.jsonData.recommendations.assessment;
            this.recommendations = this.jsonData.recommendations.comment_out.filter(item => item !== '');
          }
          this.generaGraphics(idPatient);
        } else {
          this.flagData = false;
          this.userSelected = false;
        }

      });
    } else {
      this.pouchDBService.getLastEpochFromPatient(idPatient).subscribe((result) => {
        this.supervision = new Supervision();
        setTimeout(() => {
          this.blockUI.stop(); 
        }, 1000);
        result = result[0];
        if (result && result.doc) {
          this.flagData = true;
          this.jsonData = result.doc;
          const d = new Date(this.jsonData.time_variables.date_created_utc + ' UTC');
          this.demographics = {
            MRN: this.jsonData.MRNa,
            Gender: this.jsonData.demographics.gender === 1 ? 'Male' : 'Female',
            Age: `${this.jsonData.demographics.age} years old`,
            Dx1: this.jsonData.demographics.diagnosis,
            Covid: this.jsonData.demographics.covid_19 ? 'Yes' : 'No',
            SAPS: this.jsonData.demographics.saps,
            SOFA: this.jsonData.demographics.sofa,
            Update: d,
          };
          if (typeof this.jsonData.recommendations != "undefined") {
            this.assessment = this.jsonData.recommendations.assessment;
            this.recommendations = this.jsonData.recommendations.comment_out.filter(item => item !== '');
          }
          this.generaGraphics(idPatient);
          const date = new Date();
          const ngbEndDate = { day: date.getDate(), month: date.getMonth() + 1, year: date.getFullYear() };
          const ngbInitDate = { day: 1, month: 1, year: 2000 };
          const endDate = this.formatter.format(ngbEndDate);
          const startDate = this.formatter.format(ngbInitDate);
          this.pouchDBService.getListEpochsFromPatientByDate(idPatient, startDate, endDate).subscribe((firstLastEpoch) => {
            if (firstLastEpoch.length > 0) {
              this.lstEpochSlider = firstLastEpoch;
              const dateEpoch1 = this.auxGetSliderDataById('value', '0').split(' ')[0];
              const dateEpoch2 = this.auxGetSliderDataById('value', (firstLastEpoch.length - 1).toString()).split(' ')[0];
              this.fromDate = new NgbDate(parseInt(dateEpoch1.split('-')[0]),
                parseInt(dateEpoch1.split('-')[1]), parseInt(dateEpoch1.split('-')[2]));
              this.toDate = new NgbDate(parseInt(dateEpoch2.split('-')[0]), parseInt(dateEpoch2.split('-')[1]),
                parseInt(dateEpoch2.split('-')[2]));
            }
          });
        } else {
          this.flagData = false;
          this.userSelected = false;
          this.toastr.error(this.translate.instant('alerts.noepocasusuario') + idPatient);
        }

      });
    }
  }


