  public stopEpocasPaciente() {    
    clearInterval(this.idInterval);
    clearInterval(this.idInterval2);
    clearInterval(this.idInterval3);
    this.IndexPlay = 0;
    this.pausePlayer = !this.pausePlayer;
  }

  public backwardEpocasPaciente(idPatient) {

    if(this.idInterval2!='undefined') {
      clearInterval(this.idInterval2);
    }

    if(this.IndexPlay > 0) {
          this.idInterval3 = setInterval(() => {                                                          
            console.log("index ->",this.IndexPlay);
            this.datePlayer =  this.lstEpochSlider[this.IndexPlay].value[0];
            console.log("date Player ->",this.datePlayer); 
            this.sliderTooltip = new Date(this.datePlayer.replace(' ', 'T') + 'Z').toLocaleString();
            this.getEpocaPlay (idPatient,  this.lstEpochSlider[this.IndexPlay].id);
            this.IndexPlay = this.IndexPlay -1 ;                                                
            if(this.IndexPlay == 0) { clearInterval(this.idInterval3);  }
          },300); }
  }

  public forwardEpocasPaciente(idPatient) {
    
    if(this.idInterval!='undefined') {
      clearInterval(this.idInterval);
    }
    if(this.idInterval3!='undefined') {
      clearInterval(this.idInterval3);
    }

    this.pausePlayer = !this.pausePlayer;
    
    this.idInterval2 = setInterval(() => {                                                          
      console.log("index ->",this.IndexPlay);
      this.datePlayer =  this.lstEpochSlider[this.IndexPlay].value[0];
      console.log("date Player ->",this.datePlayer); 
      this.sliderTooltip = new Date(this.datePlayer.replace(' ', 'T') + 'Z').toLocaleString();
      this.getEpocaPlay (idPatient,  this.lstEpochSlider[this.IndexPlay].id);
      this.IndexPlay = this.IndexPlay +1;                                                
      if(this.IndexPlay == this.lstEpochSlider.length) { clearInterval(this.idInterval2); this.IndexPlay=0; }
},300);
  }



  public onPageChange(event) {
    this.blockUI.start();
    this.config.currentPage = event;
    this.blockUI.stop();
  }

  launchAlert() {
    if (!this.lstPatients || this.lstPatients.length === 0) {
      Swal.fire({
        text: this.translate.instant('epoca.msgNotPatients'),
        icon: 'info',
        confirmButtonText: 'Ok'
      });
    }
  }

  getOS() {
    let userAgent = window.navigator.userAgent,
      platform = window.navigator.platform,
      macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
      windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
      iosPlatforms = ['iPhone', 'iPad', 'iPod'],
      os = null;

    if (macosPlatforms.indexOf(platform) !== -1) {
      os = 'Mac OS';
    } else if (iosPlatforms.indexOf(platform) !== -1) {
      os = 'iOS';
    } else if (windowsPlatforms.indexOf(platform) !== -1) {
      os = 'Windows';
    } else if (/Android/.test(userAgent)) {
      os = 'Android';
    } else if (!os && /Linux/.test(platform)) {
      os = 'Linux';
    }
    console.log("Sistema" + os);
    return os;
  }


  getBrowser(): string {
    let userAgent = window.navigator.userAgent.toLocaleLowerCase();
    console.log(userAgent);
    let peso = 0;
    let browserp = "";
    if (userAgent.indexOf("safari") > -1) {
      peso = 1;
    }
    if (userAgent.indexOf("edg") > -1) {
      peso = 2;
    }
    if (userAgent.indexOf("chrome") > -1) {
      peso = 3;
    }

    console.log("peso", peso);
    browserp = peso == 1 ? "Safari" : "NoSafari";
    return browserp;
  }

  getModelosByHospital() {
    this.hospitalesService.getVentsByHospital(this.hospital).subscribe((modelos_rows) => {
      this.modelo = modelos_rows[1].Name;
    })
  }

