import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import decode from 'jwt-decode';
import * as CryptoJS from 'crypto-js';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private message: string;
  public encodedSource;
  dbServices: any;
  public header = {
    'alg': 'HS256',
    'typ': 'JWT'
  };
  constructor(private _router: Router, private http: HttpClient) {
    this.dbServices = environment.API_URL;
  }


  clear(): void {
    localStorage.clear();
  }

  /**
   * check for expiration and if token is still existing or not
   * @return {boolean}
   */
  isAuthenticated(): boolean {
    return localStorage.getItem('token') != null && !this.isTokenExpired();
  }

  isTokenExpired(): boolean {
    return false;
  }

  /**
   * this is used to clear local storage and also the route to login
   */
  logout(): void {
    this.clear();
    this._router.navigate(['/login']);
  }

  decode() {
    const json: any = {};
    if (localStorage.getItem('token')) {
      return decode(localStorage.getItem('token'));

    } else {
      return json;
    }
  }

  genericJWT(tokenUsuario, tokenName) {
    const punto = '.';
    const stringifiedHeader = CryptoJS.enc.Utf8.parse(JSON.stringify(this.header));
    const encodedHeader = this.base64url(stringifiedHeader);

    const stringifiedData = CryptoJS.enc.Utf8.parse(JSON.stringify(tokenUsuario));
    const encodedData = this.base64url(stringifiedData);

    const token = encodedHeader + punto + encodedData;
    const secret = 'Monitor';
    let signature = CryptoJS.HmacSHA256(token, secret);
    signature = this.base64url(signature);
    const signedToken = token + punto + signature;
    localStorage.setItem(tokenName, signedToken);

  }

  base64url(source) {
    // Encode in classical base64
    this.encodedSource = CryptoJS.enc.Base64.stringify(source);

    // Remove padding equal characters
    this.encodedSource = this.encodedSource.replace(/=+$/, '');

    // Replace characters according to base64url specifications
    this.encodedSource = this.encodedSource.replace(/\+/g, '-');
    this.encodedSource = this.encodedSource.replace(/\//g, '_');

    return this.encodedSource;
  }

  checkTokenServices() {
    const TWENTY_MIN_2 = 20;
    const currentDate = new Date();
    let recargarToken = false;

    if (localStorage.getItem('tokenServicios') != null) {

      const fechalocalstorage = localStorage.getItem('tokenTime').split(' ');
      const horaslocal = parseInt(fechalocalstorage[4].slice(0, 2)) * 100;
      const minutoslocal = parseInt(fechalocalstorage[4].slice(3, 5));
      const tiempo_local = horaslocal + minutoslocal;
      const horasactual = (currentDate.getHours()) * 100;
      const minutosactual = currentDate.getMinutes();
      const tiempo_actual = horasactual + minutosactual;
      const diferencia_tiempos = tiempo_actual - tiempo_local;

      console.log("tiempo local ", tiempo_local);
      console.log("tiempo actual", tiempo_actual);
      console.log("diferencia de tiempos", diferencia_tiempos);


      if (diferencia_tiempos > TWENTY_MIN_2) { //(diffMs > TWENTY_MIN) {
        console.log('Delayed by more than 20 mins');
        recargarToken = true;
      }

    } else {
      recargarToken = true;
    }
    if (recargarToken) {

      console.log('Hay que recargar token');
      this.obtenerToken().subscribe(result => {
        localStorage.removeItem('tokenServicios');
        localStorage.removeItem('tokenTime');
        localStorage.setItem('tokenServicios', result.token);
        localStorage.setItem('tokenTime', new Date().toString());
      });
    }

  }
  getHttpHeaders() {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'access-token': localStorage.getItem('tokenServicios')
      })
    };
    return httpOptions;
  }

  public obtenerToken() {
    const url = this.dbServices + '/getToken';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }
}
