  public generateData(dataValue) {
    let data = [];
    let count = dataValue.length + 1;

    for (let i = 0; i < count; i++) {
      if (i === count - 1) {
        data.push([]);
      }

      data.push([i, dataValue[i]]);

    }
    return data;
  }

