  public style(data) {
    let style = {};
    let num;
if (data <= 30) {
  num = 292 - data*3.2;
 style = { 'padding-top': num.toString() + '%' };
} else if (data <= 60) {
 data = data - 30;
 num = 195-data*3.7;
 style = { 'padding-top': num.toString() + '%' };
} else if (data < 75) {
 data = data - 60;
 num = 82-data*5.4;
 style = { 'padding-top': num.toString() + '%' };
} else {
 style = { 'padding-top': '0%' };
}
    return style;
  }


  public generateDataGol(dataValue,valorX) {
    let data = [];
    let count = dataValue.length;
    let posicion =0;

    for (let i = 0; i < count; i++) {
      posicion+=0.0357142857142857;
      if (!dataValue[i]) {
        data.push([posicion, 0]);
      } else {
        data.push([posicion, dataValue[i]]);

      }
    }
    return data;
  }

