  public breathing(data_breathing) {

    let tot;
    tot = 0;

    for (var i = 0; i < data_breathing.length; i++){
      tot = tot + data_breathing[i].value;

    }

    

    this.chart = {
      title: {
        left: 'center',
        textStyle: this.titleStyle
      },
      color: ['#003366', '#ff9a29', '#4cabce', '#e5323e', '#003366', '#ff9a29', '#4cabce'],
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'shadow'
        },
        formatter: function (params) {
          const param = params[0];
          let text = '';
          switch (param.name) {
            case "SYNCH":
              text = "Fully synchronous";
              break;
            case "VAR":
              text = "Variable breath timing";
              break;
            case "DT":
              text = "Double triggering";
              break;
            case "IE":
              text = "Ineffective efforts";
              break;
            case "CYCLE":
              text = "Cycling";
              break;
            case "COMPX":
              text = "Complex";
              break;
            case "FLOW":
              text = "Flow asynchrony";
              break;
            default:
              text = param.name;
          }

          return text;
        }
      },
      grid: {
        top: 15,
        left: 0,
        right: 0,
        bottom: 20
      },
      xAxis: {
        type: 'category',
        silent: false,
        data: ['SYNCH', 'VAR', 'DT', 'IE', 'CYCLE', 'COMPX', 'FLOW'],
        nameTextStyle: {
          fontSize: 11
        }
      },
      yAxis: {
        type: 'value',
        splitLine: {
          lineStyle: {
            show:false,
            color: '#ededed'
          }
        }
      },
      series: [
        {
          name: '',
          data: [
            { value: ((data_breathing[3].value/tot) *100).toFixed(0), name: 'SYNCH', itemStyle: { color: '#008001' } },
            { value: ((data_breathing[4].value/tot) *100).toFixed(0), name: 'VAR', itemStyle: { color: '#008001' } },
            { value: ((data_breathing[2].value/tot) *100).toFixed(0), name: 'DT', itemStyle: { color: '#fa0200' } },
            { value: ((data_breathing[6].value/tot) *100).toFixed(0), name: 'IE', itemStyle: { color: '#fa0200' } },
            { value: ((data_breathing[1].value/tot) *100).toFixed(0), name: 'CYCLE', itemStyle: { color: '#fa0200' } },
            { value: ((data_breathing[0].value/tot) *100).toFixed(0), name: 'COMPX', itemStyle: { color: '#fa0200' } },
            { value: ((data_breathing[5].value/tot) *100).toFixed(0), name: 'FLOW', itemStyle: { color: '#fa0200' } },
          ],
          type: 'bar',
          label: {
            show: true,
            position: 'inside',
            formatter: function (data) {
              return data.value + '%';
            }
          }
        }
      ]
    };

    return this.chart;
  }

  
