import { Component, OnInit } from '@angular/core';
import { DispositivosService } from 'src/app/services/dispositivos.service';
@Component({
    selector: 'app-dispositivos',
    templateUrl: './dispositivos.component.html',
    styleUrls: ['./dispositivos.component.css']
    
  })

  export class DispositivosComponent implements OnInit {

    lstDispositivos :any = [];
    pageActual = 1;
    pageNumItems = 5;
    constructor(private dispositivos: DispositivosService){

    };
    ngOnInit(): void {
        this.obtenerDispositivos();
    }

    obtenerDispositivos(){
        this.dispositivos.findAll().subscribe((rsUsuarios) => {
            this.lstDispositivos = rsUsuarios;
            console.log(this.lstDispositivos);
          });
    }
  }