import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AuthService } from '../../services/auth.service';
import { trigger, state, animate, transition, style } from '@angular/animations';
import { PouchDBService } from 'src/app/services/pouchdb.service';
import { SessionService } from 'src/app/services/session.service';
declare var $: any;


@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.css'],
  animations: [ 
    trigger( 'showMobile', [
      state('false', style({visibility: 'hidden'})),
      state('true', style({visibility: 'visible'})),
      transition ( 'false => true', [
              animate('0.5s ease')
             ]),
      transition ( 'true => false', [
              animate('0.5s ease')
      ])
      ])
  ],
})
export class LayoutComponent implements OnInit {
  @HostListener('window:beforeunload', ['$event'])
  beforeunloadHandler(event) {
       localStorage.setItem('closewindow', this.localItem.id);
  }
  selectedLanguage = 'en';
  localItem: any = {};
  lenguajeSave:any;
  sessionData: any = {};
  showMobile:string = 'false';

  fechaActual:Date = new Date();

  constructor(
     public router: Router,
     public translate: TranslateService,
     private _authService: AuthService,
     public pouchDBService: PouchDBService,
     public sessionService: SessionService) {
      if(localStorage.getItem('idiomaSeleccionado') !== null){
        translate.addLangs(['en', 'es']);
        let idioma = localStorage.getItem('idiomaSeleccionado');
        translate.currentLang=idioma;
      translate.setDefaultLang(idioma);
      }else{
      translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en');
      }
  }

  ngOnInit() {
    this.localItem = this._authService.decode();


  }

  close(){
    this.sessionData = {
      name: this.localItem.id,
      isActive: "0"
    }
    this.sessionService.update(this.sessionData).subscribe(result => {
      console.log('sesin cerrada');
    },
        
    (error) => {
      console.log('error al cerrar la sesin: ' + error)
    });
    this._authService.clear();
     this.router.navigateByUrl('/register').then(() => {
      window.location.reload();
    });
 
  }

  guardarIdioma(idioma){
    localStorage.removeItem('idiomaSeleccionado');
    localStorage.setItem('idiomaSeleccionado', idioma);
    
  }
  
showMobileMenu() {
  this.showMobile = 'true';
  $('body').addClass("fixed-position");
}

closeMobileMenu() {
  this.showMobile = 'false';
  $('body').removeClass("fixed-position");
}
}
