import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';
@Injectable({
    providedIn: 'root'
})

export class SessionService {
    apiUrl: string;
    urlSession: string;
    urlGetByUser: string;
    urlInsert: string;
    urlUpdate: string;
    urlUpdateCode: string;
    urlGetCode: string;
    urlGetCodeByUser:string;

    constructor(private http: HttpClient, private authService: AuthService) {
        this.apiUrl   = environment.API_URL;
        this.urlSession = environment.URI_SESSION;
        this.urlGetByUser = this.apiUrl + this.urlSession + environment.URI_GET_BY_USER;
        this.urlInsert = this.apiUrl + this.urlSession + environment.URI_INSERT;
        this.urlUpdate = this.apiUrl + this.urlSession + environment.URI_UPDATE;
        this.urlUpdateCode = this.apiUrl + this.urlSession + environment.URI_UPDATE_CODE;
        this.urlGetCodeByUser = this.apiUrl + this.urlSession + environment.URI_GET_CODE_BY_USER;

    }

    public getByUser(user): Observable<any> {
        return this.http.get(this.urlGetByUser + '/' + user, this.authService.getHttpHeaders());
    }

    public insert(user): Observable<any> {
        return this.http.post(this.urlInsert, user, this.authService.getHttpHeaders());
    }

    public update(user): Observable<any> {
        return this.http.post(this.urlUpdate, user, this.authService.getHttpHeaders());
    }

    public getCodeByUser(user): Observable<any> {
        return this.http.post(this.urlGetCodeByUser, user, this.authService.getHttpHeaders());
    }

    public updateCode(usercode): Observable<any> {
        return this.http.post(this.urlUpdateCode, usercode, this.authService.getHttpHeaders());
    }



}