import { Injectable } from '@angular/core';
import PouchDB from 'pouchdb';
import { environment } from '../../environments/environment';
import { ToastrService } from 'ngx-toastr';
import { EmailService } from '../services/email.service';
import PouchDBFind from 'pouchdb-find';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';
import { UserIdleService } from 'angular-user-idle';

PouchDB.plugin(PouchDBFind);


@Injectable({
  providedIn: 'root'
})

export class PouchDBService {
  dbServices: any;

  constructor(private toastr: ToastrService, public emailService: EmailService, private http: HttpClient, public authService: AuthService, private userIdle: UserIdleService) {
    this.dbServices = environment.DB_SERVICES;
    this.userIdle.resetTimer();
  }

  public login(user, password) {
    const url = this.dbServices + '/users';
    return this.http.get(url + '/' + user + '/' + password).pipe(map(json => {

      const result = json;
      return result as any[];

    }));
  }

  public insertModifyUser(user) {
    const url = this.dbServices + '/insertUsers';
    return this.http.post<any>(url, user);
  }

  public activeUser(username) {
    const url = this.dbServices + '/activeUser?username=' + username;

    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }


  public searchUserForChangePassword(username, id) {
    const url = this.dbServices + '/changePassword?username=' + username + '&uuid=' + id + '';

    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

  public getUserByEmail(email) {
    const url = this.dbServices + '/searchUserByEmail?email=' + email.toString();

    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

  public insertPatient(patient) {
    const url = this.dbServices + '/patient_epochs_ai';
    return this.http.post<any>(url, patient);
  }

  public getEpocaRandom() {
    const url = this.dbServices + '/getEpocaRamdom';

    return this.http.get(url).pipe(map(json => {      
      const result = json;
      console.log(">>>>>>>>>>>>>>>>>>>")      
      console.log("result",result);
      return result as any;
    }));
  }

  public postHospitals(hospital) {
    const url = this.dbServices + '/hospitals';
    return this.http.post<any>(url, hospital);
  }

  public updateHospital(id, hospital, pais, estatus) {
    const url = this.dbServices + '/updateHospital?id=' + id + '&hospital=' + hospital + '&pais=' + pais + '&estatus=' + estatus;
    return this.http.get<any>(url);
  }

  public getPatientsByUser(idUser) {

    const url = this.dbServices + '/searchPatient?user=' + idUser;
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getLastEpochFromPatient(idPatient) {
    const url = this.dbServices + '/lastEpochs?patient_id=' + idPatient;

    return this.http.get(url).pipe(map(json => {
      const res = json;
      return res as any;
    }));

  }

  public getEpochFromPatientById(idEpoch) {

    const url = this.dbServices + '/patient_epoch_ai/' + idEpoch;

    return this.http.get(url).pipe(map(json => {
      const res = json;
      console.log('resp epoch: ' + JSON.stringify(res));
      return res as any;
    }));

  }

  public getListEpochsFromPatient(idPatient) {

    const url = this.dbServices + '/checkEpochs?patient_id=' + idPatient;

    return this.http.get(url).pipe(map(json => {
      const res = json;
      return res as any[];
    }));

  }

  public getListEpochsFromPatientByDate(idPatient, initDate, endDate) {

    const url = this.dbServices + '/checkEpochs?patient_id=' + idPatient
      + '&init_date=' + initDate
      + '&last_date=' + endDate;

    return this.http.get(url).pipe(map(json => {
      const res = json;
      return res as any[];
    }));

  }

  public getDataGraph(epochDateUtc, idPatient) {
    epochDateUtc = epochDateUtc.split('.')[0].replace(' ', 'T');
    const url = this.dbServices + '/graphOne?last_date=' + epochDateUtc + '&patient_id=' + idPatient;

    return this.http.get(url).pipe(map(json => {
      const res = json;
      return res as any;
    }));

  }

  public getPatientsActivosByUser(idUser) {

    const url = this.dbServices + '/searchActivePatient?user=' + idUser;
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public postAlerta(alert) {
    const url = this.dbServices + '/alerts';
    return this.http.post<any>(url, alert);
  }

  public getPatientsSuperAdmin() {

    const url = this.dbServices + '/searchPatients';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getPatientsActivSuperAdmin() {

    const url = this.dbServices + '/searchActivePatientAdmin';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getPatientsActivosAdmHospital(hospital) {

    const url = this.dbServices + '/searchPatientsAdminHospital?hospital=' + hospital;
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getAllPatientsAdmHospital(hospital) {

    const url = this.dbServices + '/searchPatientsAdminHospitalAll?hospital=' + hospital;
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public insertVentilador(ventilador) {
    const url = this.dbServices + '/insertVentilador';
    return this.http.post<any>(url, ventilador);
  }

  public updateVentilador(id, ventilador, registro, numeroSerie, vigencia, estatus, correo, nombre) {
    const url = this.dbServices + '/updateVentilador?id=' + id + '&ventilador=' + ventilador + '&registro='
      + registro + '&numeroSerie=' + numeroSerie + '&vigencia=' + vigencia + '&estatus=' + estatus
      + '&correo=' + correo + '&nombre=' + nombre;
    return this.http.get<any>(url);
  }

  public insertToken(token) {
    const url = this.dbServices + '/insertToken';
    return this.http.post<any>(url, token);
  }

  public updateUser(id, passOrIdpass, option) {
    const url = this.dbServices + '/updateUser?id=' + id + '&passOrIdpass=' + passOrIdpass + '&option=' + option;
    return this.http.get<any>(url);
  }

  public setSession(sessionData) {
    const url = this.dbServices + '/setSession';
    return this.http.post<any>(url, sessionData);
  }

  public getSession(user) {
    const url = this.dbServices + '/getSession?user=' + user;
    return this.http.get<any>(url);
  }

  public updateSession(user, isActive) {
    const url = this.dbServices + '/updateSession?user=' + user + '&isActive=' + isActive;
    return this.http.get<any>(url);
  }

}
