import { Injectable } from '@angular/core';
import PouchDB from 'pouchdb';
import PouchDBFind from 'pouchdb-find';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';
import { UserIdleService } from 'angular-user-idle';

PouchDB.plugin(PouchDBFind);

@Injectable({
  providedIn: 'root'
})

export class ChartService {

  dbServices: any;

  constructor(private http: HttpClient, public authService: AuthService, private userIdle: UserIdleService) {
    this.dbServices = environment.DB_SERVICES;
    this.userIdle.resetTimer();

  }

  public getEpocasPacientes() {

    const url = this.dbServices + '/getCountPacientesConEpoca';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getCountrys() {

    const url = this.dbServices + '/getCountUsersCountries';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

  public getHospitals() {
    const url = this.dbServices + '/getHospitals';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

  public getPatientsByHospital() {

    const url = this.dbServices + '/getPatientsByHospital';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));
  }

  public getPatients() {

    const url = this.dbServices + '/getCountPacientes';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }


  public getEpocas() {

    const url = this.dbServices + '/getCountEpocas';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));

  }

  public getEpocasXmes() {

    const url = this.dbServices + '/getEpocasPorMes';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));

  }

  public getDateCreatePatients() {
    const date = new Date();
    const yearPast = date.getFullYear() - 1;
    const yearActual = date.getFullYear();
    const month = date.getMonth() + 1;

    const url = this.dbServices + '/getCreatedDateOfPatients?pastYear=' + yearPast
      + '&actualYear=' + yearActual + '&month=' + month;
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));
  }

  getSupervisados() {

    const url = this.dbServices + '/getPacientesSupervisados';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));

  }

  getEpocasSupervisadas() {

    const url = this.dbServices + '/getEpocasSupervisadas';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));

  }

  getExpertsEpocas() {

    const url = this.dbServices + '/getExpertsEpocas';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any[];
    }));
  }

  public getAlerts() {
    const url = this.dbServices + '/getAlerts';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));

  }

  public getVentiladores() {
    const url = this.dbServices + '/getVentiladores';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

  public getModelos() {
    const url = this.dbServices + '/getModelos';
    return this.http.get(url).pipe(map(json => {
      const result = json;
      return result as any;
    }));
  }

}