import { Component, OnInit, Input } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { EmailService } from 'src/app/services/email.service';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../../environments/environment';
@Component({
  selector: 'app-contacto',
  templateUrl: './contacto.component.html',
  styleUrls: ['./contacto.component.css']
})
export class ContactoComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;
  @Input() admin: boolean;
  idioma: string;
  flagRegister = false;
  constructor(private formBuilder: FormBuilder,
              private emailService: EmailService,
              public translate: TranslateService,
              private toastr: ToastrService) {
    window.scrollTo(0, 0);
    translate.addLangs(['en', 'es']);
    translate.setDefaultLang('en');
    this.idioma = this.translate.currentLang;
  }
  contactoForm: FormGroup;
  submitted = false;

  contacto = /*Contacto =*/ {
    nombre: '',
    correo: '',
    mensaje: '',
    asunto: ''
  };

  ngOnInit(): void {
    this.contactoForm = this.formBuilder.group({
      correo: ['', [Validators.required, Validators.email, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
      nombre: ['', [Validators.required]],
      mensaje: ['', [Validators.required]],
      asunto: ['', [Validators.required]]
  });
  }

  get f() { return this.contactoForm.controls; }


  enviarMail() {
    this.submitted = true;
    if (this.contactoForm.invalid) {
        return;
    }
    this.blockUI.start();
    const bodyEmail = {
      'subject': this.contacto.asunto,
      'from': environment.EMAIL_FROM,
      'to': environment.EMAIL_CONTACTO,
      'html': this.generatehtmlContacto(this.contacto.mensaje, this.contacto.nombre, this.contacto.correo)
    };
    this.emailService.sendGenericEmail(bodyEmail).subscribe((resemail) => {
      this.blockUI.stop();
      this.toastr.success('An email was sent to: ' + bodyEmail.to);
      this.cleanForm();
    },
      (error) => {
        this.blockUI.stop();
        this.toastr.error('Error al enviar correo de: ' + bodyEmail.to);
      });
 }

 public generatehtmlContacto(msg, nombre, email) {
  const html = '<div style=\'text-align: left;\'>' +
    '<div class=\'row\'style=\'text-align: left;\'>' +
    '<img src=\'https://aeolus01e4ce.blob.core.windows.net/demo02/logo_VentSupport.ico\' style=\'width: 7%;\' ></div>' +
    '<br>' +
    '<h3>Nombre:</h3><b>'+ nombre +'</b><br>' +
    '<h3>Correo:</h3><b>'+ email +'</b><br>' +
    '<h3>Comentarios:</h3><br>' +
    '<p style=\'padding: 10px 20px 10px 20px; border-radius: 5px;color: black; font-size: 14px; \' >'
    + msg + '</p>' +
    '</div>';

  return html;
}

cleanForm(){
  this.submitted = false;
  this.contactoForm.reset();
}

omit_special_char(event, type) {
  var regex;

  switch (type) {
    case 1:
      regex = new RegExp("^[a-zA-Z0-9]+$");
      break;
    case 2:
      regex = new RegExp("^[a-zA-Z\ \]+$");
      break;
    case 3:
      regex = new RegExp("^[a-zA-Z0-9\.\_\@\-\]+$");
      break;
    default:
      regex = new RegExp("^[a-zA-Z0-9]+$");
      break;
  }

  var key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
  if (!regex.test(key)) {
     event.preventDefault();
     return false;
  } 
}

}
