import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';
@Injectable({
    providedIn: 'root'
})

export class UsersService {
    apiUrl: string;
    urlUsers: string;
    urlLogin: string;
    urlInsert: string;
    urlUpdateIdPass: string;
    urlUpdatePass: string;
    urlFindByEmail: string;
    urlFindByIdPassAndUser: string;
    urlGetUsersDemo:string;
    urlActivateUserDemo:string;

    constructor(private http: HttpClient, private authService: AuthService) {
        this.apiUrl   = environment.API_URL;
        this.urlUsers = environment.URI_USERS;
        this.urlLogin = this.apiUrl + this.urlUsers + environment.URI_LOGIN;
        this.urlInsert = this.apiUrl + this.urlUsers + environment.URI_INSERT;
        this.urlUpdateIdPass = this.apiUrl + this.urlUsers + environment.URI_UPDATE_IDPASS;
        this.urlUpdatePass = this.apiUrl + this.urlUsers + environment.URI_UPDATE_PASS;
        this.urlFindByEmail = this.apiUrl + this.urlUsers + environment.URI_FIND_BY_EMAIL;
        this.urlFindByIdPassAndUser = this.apiUrl + this.urlUsers + environment.URI_FIND_BY_IDPASS_AND_USER;
        this.urlGetUsersDemo = this.apiUrl + this.urlUsers + environment.URI_GET_USERS_DEMO;
        this.urlActivateUserDemo  = this.apiUrl + this.urlUsers + environment.URI_ACTIVATE_USERS_DEMO;
    }

    public login(user): Observable<any> {
        return this.http.post(this.urlLogin, user, this.authService.getHttpHeaders());
    }

    public insert(user): Observable<any> {
        return this.http.post(this.urlInsert, user, this.authService.getHttpHeaders());
    }

    public updateIdPass(user): Observable<any> {
        return this.http.post(this.urlUpdateIdPass, user, this.authService.getHttpHeaders());
    }

    public updatePass(user): Observable<any> {
        return this.http.post(this.urlUpdatePass, user, this.authService.getHttpHeaders());
    }

    public findByEmail(email): Observable<any> {
        return this.http.get(this.urlFindByEmail + '/' + email, this.authService.getHttpHeaders());
    }

    public findByIdPassAndUser(user): Observable<any> {
        return this.http.post(this.urlFindByIdPassAndUser, user, this.authService.getHttpHeaders());
    }
    
    public getUsersDemo(): Observable<any> {
        return this.http.get(this.urlGetUsersDemo, this.authService.getHttpHeaders());
    }

    public activateUserDemo(userDemo): Observable<any> {
        return this.http.post(this.urlActivateUserDemo,userDemo, this.authService.getHttpHeaders());
    }

}