  /* funcion que abre modal generico */
  open(content, value) {

    if (value == 3) {
      this.modalOptions = {
        backdrop: 'static',
        backdropClass: 'customBackdrop',
        size: 'lg'
      }
    }

    this.modalService.open(content, this.modalOptions).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }


  openUserDemoModal(content, value) {

    this.blockUI.start();

    this.modalData = value;

    this.idUserDemo = this.modalData.id;
    this.nameUserDemo = this.modalData.name;
    this.nameUserDemoModal = this.modalData.name;
    this.countryUserDemo = this.modalData.country;
    this.hospitalUserDemo = this.modalData.hospital;
    this.simulacionUserDemo = this.modalData.modSimulacion;

 
    this.filtrarHospitales(this.modalData.country);

    
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
      this.blockUI.stop();
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      this.submittedModal = false;
      this.blockUI.stop();
    });
  }




  /* funcion para detectar acciones con teclado  */
  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }


  @HostListener('scroll', ['$event'])
  onScroll(event: any) {
    if ((event.target.scrollTop || event.body.scrollTop) + event.target.offsetHeight + 30 >= event.target.scrollHeight) {
      this.readConditions = true;
    }
  }

  @HostListener("document:scroll")
  scrollfunction() {
    if (document.body.scrollTop > 0 || document.documentElement.scrollTop > 0) {
      this.showuper = true;
    }
    else {
      this.showuper = false;
    }
  }


