  public formatUser() {
    this.userObject._id = this.user._id;
    this.userObject.name = this.user.name.toUpperCase();
    this.userObject.email = this.user.email.toUpperCase();
    this.userObject.country = this.user.country.toUpperCase();
    this.userObject.status = true;
    this.userObject.password = sha256(this.user.password);
    this.userObject.rol = parseInt(this.user.rol);
    if (this.user.hospital) {
      this.userObject.hospital = this.user.hospital;
    } else {
      this.userObject.hospital = 'Hospital Vent Support';
    }
    if (!this.admin) {
      this.userObject.modoSimulacion = 1;
    } else {
      this.userObject.modoSimulacion = 0;
    }
    
    this.userObject.dateRegister = new Date().toLocaleString();
    this.userObject.serial = this.serial;
    this.userObject.statusSerial = false;
  }



  public read() {
    if (this.readModal === 1) {
      this.userObject.license = true;
    } else if (this.readModal === 2) {
      this.userObject.politics = true;
    } else if (this.readModal === 3) {
      this.userObject.termsConditions = true;
    }
    this.cancel();
  }

