  openLogin(content) {

    this.modalOptions = {
      backdrop: 'static',
      backdropClass: 'customBackdrop',
      size: 'md'
    }

    this.modalService.open(content, this.modalOptions).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });

  }

  onSubmitLogin() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.registerFormLogin.invalid) {
      return;
    }
    let password = sha256(this.usuario.pass);
    let user = '';
    if (this.regexcorreo.test(this.usuario._id)) {
      user = this.usuario._id.toUpperCase();

    } else {
      user = this.usuario._id;
    }
    this.user = {
      id: user,
      password: password
    }
    
    this.sessionService.getByUser(user).subscribe(result => {
    
      if (result.length > 0) {
        if (!!result[0].isActive) {

          //this.emailTo = 
          this.changeCode();
          //this.sessionActive = true;
          return;
        } else {
          this.usersService.login(this.user).subscribe((result) => {
            if (result.codigo === '003') {
              this.toastr.error(this.translate.instant('alerts.usernamenotexist'));
              return;
            } else if (result.codigo === '002') {
              this.toastr.error(this.translate.instant('alerts.wrongpass'));
              return;
            } else {
              this.sessionData = {
                name: user,
                isActive: "1"
              }
              this.sessionService.update(this.sessionData).subscribe(result => {

              },
                (error) => {
                  this.toastr.error(this.translate.instant('alerts.errorgeneral'));
                  console.log('error al actualizar la sesion: ' + error)
                  return;
                });

              this.authService.genericJWT(result.codigo, 'token');
              this.toastr.success(this.translate.instant('alerts.userpasscorrect'));
              if (!result.codigo.rol) {
                this.router.navigateByUrl('/download').then(() => {
                  window.location.reload();
                });
              } else {
                this.router.navigateByUrl('/monitor').then(() => {
                  window.location.reload();
                });
              }
              this.cancel();
            }


          },
            (error) => {
              this.toastr.error(this.translate.instant('alerts.errorgeneral'));
              console.log('error en login: ' + error)
              return;
            });
        }

      } else {
        this.sessionData = {
          name: user,
          isActive: "1"
        }
        this.usersService.login(this.user).subscribe((result) => {
          if (result.codigo === '003') {
            this.toastr.error(this.translate.instant('alerts.usernamenotexist'));
            return;
          } else if (result.codigo === '002') {
            this.toastr.error(this.translate.instant('alerts.wrongpass'));
            return;
          } else {
            this.sessionService.insert(this.sessionData).subscribe(result => {

            },
              (error) => {
                this.toastr.error(this.translate.instant('alerts.errorgeneral'));
                console.log('error al activar la sesion: ' + error)
                return;
              });

            this.authService.genericJWT(result.codigo, 'token');
            this.toastr.success(this.translate.instant('alerts.userpasscorrect'));
            if (!result.codigo.rol) {
              this.router.navigateByUrl('/download').then(() => {
                window.location.reload();
              });
            } else {
              this.router.navigateByUrl('/monitor').then(() => {
                window.location.reload();
              });
            }
            this.cancel();
          }


        },
          (error) => {
            this.toastr.error(this.translate.instant('alerts.errorgeneral'));
            console.log('error en login: ' + error)
            return;
          });
      }
    },
      (error) => {
        console.log('error al obtener la sesion: ' + error)
      });

  }

