  omit_special_char(event, type) {
    var regex;

    switch (type) {
      case 1:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        this.registerForm.get('pass').enable();
        break;
      case 2:
        regex = new RegExp("^[a-zA-Z\ \]+$");
        break;
      case 3:
        regex = new RegExp("^[a-zA-Z0-9\.\_\@\-\]+$");
        this.registerForm.get('user').enable();
        break;
      default:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        break;
    }

    var key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
    if (!regex.test(key)) {
      event.preventDefault();
      return false;
    }
  }

