import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';

@Injectable({
    providedIn: 'root'
})

export class VentiladoresService {
    apiUrl: string;
    urlVentiladores: string;
    urlInsert: string;
    urlUpdate: string;
    urlFindAll: string;
    urlGetVentsByHospital: string;

    constructor(private http: HttpClient,private authService: AuthService) {
        this.apiUrl   = environment.API_URL;
        this.urlVentiladores = environment.URI_VENTILADORES;        
        this.urlFindAll = this.apiUrl + this.urlVentiladores + environment.URI_FIND_ALL;
        this.urlInsert = this.apiUrl + this.urlVentiladores + environment.URI_INSERT;        
        this.urlUpdate = this.apiUrl + this.urlVentiladores + environment.URI_UPDATE;
        this.urlGetVentsByHospital = this.apiUrl + this.urlVentiladores + environment.URI_GET_VENTS_BY_HOSPITAL;        
    }

    public findAll(): Observable<any> {
        console.log(this.urlFindAll)
        return this.http.get(this.urlFindAll,this.authService.getHttpHeaders());
    }

    public insert(ventilador): Observable<any> {
        return this.http.post(this.urlInsert, ventilador,this.authService.getHttpHeaders());
    }

    public update(ventilador): Observable<any> {
        return this.http.post(this.urlUpdate, ventilador,this.authService.getHttpHeaders());
    }
    
    public getVentsByHospital(hospital): Observable<any> {
        return this.http.post(this.urlGetVentsByHospital,hospital, this.authService.getHttpHeaders());
    }


}