import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { monitorRoutes } from './monitor.routes';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AuthGuard } from '../guards/auth-guard.service';
import { RoleGuard } from '../guards/role-guard.service';
import { PatientComponent } from './patient/patient.component';
import { DownloadComponent } from './download/download.component';
import { EpocaComponent } from './epoca/epoca.component';
import { ContactoComponent } from './contacto/contacto.component';
import { HistoricalComponent } from './historical/historical.component';
import { ReportsComponent } from './reports/reports.component';
import { UsersComponent } from './users/users.component';
import { RegisterComponent } from './register/register.component';
import { HospitalesComponent } from './hospitales/hospitales.component';
import { AlertasComponent } from './config_alertas/alertas.component';
import { LoginComponent } from './login/login.component';
import { PoliticsComponent } from './politics/politics.component';
import { TermsConditionsComponent } from './terms-conditions/terms-conditions.component';
import { LicenseComponent } from './license/license.component';
import { RecaptchaModule, RecaptchaFormsModule, RECAPTCHA_LANGUAGE } from 'ng-recaptcha';
import { NgxEchartsModule } from 'ngx-echarts';
import { BlockUIModule } from 'ng-block-ui';
import { UserDirective } from './directives/username.directive';
import { LoginBaseComponent } from './login_base/loginbase.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { DateRangePickerModule } from '@syncfusion/ej2-angular-calendars';
import { LicenciamientoComponent } from './licenciamiento/licenciamiento.component';
import { ColumnaFechaPipe } from './columnafechapipe.pipe';
import { infoComponent } from './info/info.component';
import { DispositivosComponent } from './dispositivos/dispositivos.component';
import { RecommendationsComponent } from './recommendations/recommendations.component'

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(monitorRoutes),
    NgbModule,
    BrowserAnimationsModule,
    NgxEchartsModule,
    FormsModule,
    RecaptchaModule,  //this is the recaptcha main module
    RecaptchaFormsModule, //this is the module for form incase form validation
    ReactiveFormsModule,
    BlockUIModule.forRoot(),
    NgxPaginationModule,
    Ng2SearchPipeModule,
    DateRangePickerModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  providers: [
    LayoutComponent,
    AuthGuard,
    RoleGuard,
    {
      provide: RECAPTCHA_LANGUAGE,
      useValue: 'en', 
    },
  ],
  declarations: [
     LayoutComponent,
     PatientComponent,
     EpocaComponent,
     ContactoComponent,
     HistoricalComponent,
     ReportsComponent,
     UsersComponent,
     HospitalesComponent,
     AlertasComponent,
     RegisterComponent,
     LoginComponent,
     PoliticsComponent,
     TermsConditionsComponent,
     LicenseComponent,
     UserDirective,
     LoginBaseComponent,
     ChangePasswordComponent,
     DownloadComponent,
     LicenciamientoComponent,
     DispositivosComponent,
     infoComponent,
     ColumnaFechaPipe,
     RecommendationsComponent     
    ]
})
export class MonitorModule { }
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(httpClient, "./assets/i18n/", ".json");
}