import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';

@Injectable({
    providedIn: 'root'
})

export class HospitalesService {
    apiUrl: string;
    urlHospitales: string;
    urlFindAll: string;
    urlGetAllBy: string;
    urlGetVentsByHospital: string;
    urlInsert: string;
    urlUpdate: string;

    constructor(private http: HttpClient, private authService: AuthService) {
        this.apiUrl   = environment.API_URL;
        this.urlHospitales = environment.URI_HOSPITALES;
        this.urlFindAll = this.apiUrl + this.urlHospitales + environment.URI_FIND_ALL;
        this.urlGetAllBy = this.apiUrl + this.urlHospitales + environment.URI_GET_ALL_BY_COUNTRY;
        this.urlGetVentsByHospital = this.apiUrl + this.urlHospitales + environment.URI_GET_VENTS_BY_HOSPITAL;        
        this.urlInsert = this.apiUrl + this.urlHospitales + environment.URI_INSERT;
        this.urlUpdate = this.apiUrl + this.urlHospitales + environment.URI_UPDATE;
    }

    public findAll(): Observable<any> {
        return this.http.get(this.urlFindAll, this.authService.getHttpHeaders());
    }

    public getAllBy(country): Observable<any> {
        return this.http.post(this.urlGetAllBy,country, this.authService.getHttpHeaders());
    }

    public getVentsByHospital(hospital): Observable<any> {
        return this.http.post(this.urlGetVentsByHospital,hospital, this.authService.getHttpHeaders());
    }

    public insert(hospital): Observable<any> {
        return this.http.post(this.urlInsert, hospital, this.authService.getHttpHeaders());
    }

    public update(hospital): Observable<any> {
        return this.http.post(this.urlUpdate, hospital, this.authService.getHttpHeaders());
    }

}