import { Component, OnInit, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AuthService } from '../../services/auth.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CustomvalidationService } from '../../services/customvalidation.service';
import { sha256 } from 'js-sha256';
import { PouchDBService } from 'src/app/services/pouchdb.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';




@Component({
  selector: 'app-loginbase',
  templateUrl: './loginbase.component.html',
  styleUrls: ['./loginbase.component.css']
})

export class LoginBaseComponent implements OnInit {
  @Input() admin: boolean;
  localItem: any = {};
  flagRegister = false;
  registerForm: FormGroup;
  submitted = false;
  usuario: any = {};
  unamePattern = "^[a-zA-Z0-9\-\_\.]+$";
  regexcorreo = new RegExp("^[_a-z0-9-]+(.[_a-z0-9-]+)*@[a-z0-9-]+(.[a-z0-9-]+)*(.[a-z]{2,4})$");
  constructor(public translate: TranslateService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private customValidator: CustomvalidationService,
    public router: Router,
    public pouchDBService: PouchDBService,
    private toastr: ToastrService) {
    if (localStorage.getItem('idiomaSeleccionado') !== null) {
      translate.addLangs(['en', 'es']);
      let idioma = localStorage.getItem('idiomaSeleccionado');
      translate.currentLang = idioma;
      translate.setDefaultLang(idioma);
    } else {
      translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en');
    }
  }


  ngOnInit(): void {
    this.registerForm = this.formBuilder.group({
      username: ['', [Validators.required]],
      password: ['', [Validators.required]]
    });
  }



  close() {
    this.authService.clear();
    this.localItem = this.authService.decode();
  }

  guardarIdioma(idioma) {
    localStorage.removeItem('idiomaSeleccionado');
    localStorage.setItem('idiomaSeleccionado', idioma);
  }
  
  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.registerForm.invalid) {
        return;
    }
    let password= sha256(this.usuario.pass);
    let user= '';
    if(this.regexcorreo.test(this.usuario._id)){
      user = this.usuario._id.toUpperCase();

    }else{
      user = this.usuario._id;
    }

    this.pouchDBService.login(user, password).subscribe((result) => {
     
      if(result['status']){
        this.authService.genericJWT(result, 'token');
        this.toastr.success('The username and password are correct.');
        if(!result['rol']){
          this.router.navigate(['/download']);
        }else{
          this.router.navigate(['/monitor']);

        }
      }else{
        this.toastr.info('User not activated.');
      }

   
  },
  (error) => {
    if (error.error === '001') {
      this.toastr.error('Wrong password please enter it correctly.');
    } else if (error.error === '002') {
      this.toastr.error('Internet connection error.');
    } else {
      this.toastr.error('Username does not exist.');
    }
  });
  }

  // convenience getter for easy access to form fields
  get f() { return this.registerForm.controls; }

  public download(){
    this.router.navigate(['/download']);
  
  }
}