import { Component, OnInit ,HostListener } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ChartService } from 'src/app/services/chart.service';  
import {NgbModal, ModalDismissReasons, NgbModalOptions} from '@ng-bootstrap/ng-bootstrap';
import { PouchDBService } from '../../services/pouchdb.service';
import { ToastrService } from 'ngx-toastr';
import { AuthService } from '../../services/auth.service';


@Component({
    selector: 'app-alertas',
    templateUrl: './alertas.component.html',
    styleUrls: ['./alertas.component.css']
  })

export class AlertasComponent implements OnInit {
    idioma: string;
    registerForm: FormGroup;

    user: any = {};
    userObject: any = {};
    selectedVariables:any= {};
    selectedVariablesNo:any= {};

    listaUsuarios:any=[];
    ltsAsignados:any=[];
    ltsNoAsignados:any=[];

    lstHospitales:any=[];
    pageActual = 1;
    pageNumItems = 5;

    readModal: number;
    readConditions = false;

    unamePattern = "^[a-zA-Z0-9\-\_\.]+$"; 
    submitted = false;
    closeResult: string;
    modalOptions: NgbModalOptions;
    localItem: any = {};
    consecutivo:string="";
    lstHospitales2:any=[];

    lstAlerts:any=[];
    lstAlertsHospital:any=[];

    constructor(public translate: TranslateService,
        private formBuilder: FormBuilder,
        public chartService: ChartService,
        public pouchDBService: PouchDBService,
        private modalService: NgbModal,
        private toastr: ToastrService,
        private _authService: AuthService){
            this.modalOptions = {
                backdrop: 'static',
                backdropClass: 'customBackdrop',
                size: 'lg'
              }
        translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en'); 
      this.idioma = this.translate.currentLang;
    }

    ngOnInit(): void {
        this.registerForm = this.formBuilder.group({
            country: ['', [Validators.required]],
            hospital: ['', [Validators.required]],
            tituloAlerta:['',[Validators.required]],
            estatus:['',[Validators.required]],
            descripcion:['',[Validators.required]]
        });
        this.localItem = this._authService.decode();
        this.user.country='';
        this.user.hospital='';
        this.obtenerHospitales();
        
    }
 
    onSubmit(){
       this.submitted = true;
      // stop here if form is invalid
      if (this.registerForm.invalid ) {
          return;
      }
      this.formatUser();
      this.pouchDBService.postAlerta(this.userObject).subscribe(result => {
        if(result.ok){
            this.limpiar();
            this.limpiarAlert();
            this.toastr.success('Alert  Registration correctly.');
        }

        },(error) => {
          this.toastr.error('Alert  Registration failed');
        });
        
    }

    onReset() {
        this.submitted = false;
        this.registerForm.reset();
    }

    public formatUser(){
      this.consecutivo =this.lstAlerts.length +1;
        this.userObject._id = this.consecutivo.toString();
         this.userObject.country =  this.user.country.toUpperCase();
         this.userObject.hospital= this.user.hospital.toUpperCase();
         this.userObject.tituloAlerta= this.user.tituloAlerta;
         this.userObject.estatus= this.user.estatus.toUpperCase(); 
         this.userObject.descripcionAlerta= this.user.descripcion;  
         this.userObject.fecha_creacion= new Date().toJSON();
         console.log("Enviar"+JSON.stringify(this.userObject));
      }

    obtenerHospitales(){
        this.chartService.getHospitals().subscribe((rsUsuarios) => {
           this.lstHospitales2= rsUsuarios.rows;
        });
      }
    obtenerAlertas(hospital){
      console.log("hostal-----"+this.user.hospital);
      if(this.user.hospital !==''&& this.user.hospital !== null){
        console.log("Entra");
        this.chartService.getAlerts().subscribe((rsUsuarios) => {
          this.lstAlerts= rsUsuarios.rows;
          this.lstAlertsHospital=[];
          
         this. lstAlerts.forEach(element => {
         if(element.value.hospital===hospital){
          this.lstAlertsHospital.push(element);
         }
          });
          if(this.lstAlertsHospital.length ===0&&this.user.hospital!==''){
            this.toastr.warning('There are no alerts registered for this hospital');
          }
        
        });
      }
    }
    limpiarAlert(){
      this.lstAlertsHospital=[];
    }

    get f() { return this.registerForm.controls; }

    public read(){
         if(this.readModal === 3){
         this.userObject.termsConditions = true; 
       } 
       this.cancel();
   }

   cancel(){
    this.modalService.dismissAll();
    this.readConditions = false;

  }

  @HostListener('scroll', ['$event']) 
  onScroll(event: any) {
   if ((event.target.scrollTop || event.body.scrollTop) + event.target.offsetHeight + 30 >= event.target.scrollHeight) {
      this.readConditions = true;

    }
 }

   /* funcion que abre modal generico */
   open(content, value) {
    this.modalService.open(content, this.modalOptions).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
     });
   }

   /* funcion para detectar acciones con teclado  */
  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  public limpiar(){
    this.onReset(); 
    this.registerForm = this.formBuilder.group({
        country: ['', [Validators.required]],
        hospital: ['', [Validators.minLength(5),Validators.required]],
        tituloAlerta:['',[Validators.required]],
        estatus:['',[Validators.required]],
        descripcion:['',[Validators.required]]

    });
    this.user.country='';

  }

  public filtrarHospitales(country){
    if( country === "MEXICO"){
      this.lstHospitales=  this.lstHospitales2.filter(mexico => mexico['value']['country']=== "MEXICO");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "EUA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "EUA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "ARGENTINA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "ARGENTINA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "BRASIL"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "BRASIL");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "COLOMBIA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "COLOMBIA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "COSTA_RICA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "COSTA_RICA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "ECUADOR"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "ECUADOR");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "PANAMA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "PANAMA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "PERU"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "PERU");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if(country === "REPUBLICA_DOMINICANA"){
      this.lstHospitales=  this.lstHospitales2.filter(eu => eu['value']['country']=== "REPUBLICA_DOMINICANA");
      if(this.lstHospitales.length === 0){
        this.toastr.warning('There are no registered hospitals in this country');
      }
    }else if( country === ""){
      this.lstHospitales=[];
    }
    if(this.lstHospitales.length > 0){
      this.registerForm.get('hospital').enable();
    }else{
      this.registerForm.get('hospital').disable();
    }
  }

}