import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { AuthService } from './auth.service';


@Injectable({
    providedIn: 'root'
})

export class Vent_modelosService {
    apiUrl: string;
    urlVent_modelos: string;
    urlFindAll: string;

    constructor(private http: HttpClient, private authService: AuthService) {
        this.apiUrl   = environment.API_URL;
        this.urlVent_modelos = environment.URI_VENT_MODELOS;
        this.urlFindAll = this.apiUrl + this.urlVent_modelos + environment.URI_FIND_ALL;
    }

    public findAll(): Observable<any> {
        console.log(this.urlFindAll)
        return this.http.get(this.urlFindAll, this.authService.getHttpHeaders());
    }
    

}