import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { PouchDBService } from '../../services/pouchdb.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CustomvalidationService } from '../../services/customvalidation.service';
import { MustMatch } from '../../_helpers/must-match.validator';
import { TranslateService } from '@ngx-translate/core';
import { sha256 } from 'js-sha256';
import { UsersService } from 'src/app/services/users.service';


@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {

  username = '';
  uuid = '';
  registerForm: FormGroup;
  submitted = false;
  userData: any = {};
  user: any = {};
  password = '';



  constructor(
    private route: ActivatedRoute,
    private _router: Router,
    private toastr: ToastrService,
    public pouchDBService: PouchDBService,
    private formBuilder: FormBuilder,
    private customValidator: CustomvalidationService,
    public translate: TranslateService,
    private usersService: UsersService

  ) {
    if (localStorage.getItem('idiomaSeleccionado') !== null) {
      translate.addLangs(['en', 'es']);
      let idioma = localStorage.getItem('idiomaSeleccionado');
      translate.currentLang = idioma;
      translate.setDefaultLang(idioma);
    } else {
      translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en');
    }
  }

  ngOnInit(): void {


    this.registerForm = this.formBuilder.group({
      pass: ['', [Validators.required, Validators.minLength(8), this.customValidator.patternValidator()]],
      pass2: ['', [Validators.required]]

    },
      {
        validator: MustMatch('pass', 'pass2')
      });


    this.route.queryParamMap.subscribe(
      (params: Params) => {
        this.username = params.params.username
        this.uuid = params.params.id;
        if (!this.username || !this.uuid) {
          this._router.navigate(['/register']);
        } else {
          this.userData = {
            idChangePassword: this.uuid,
            name: this.username
          }
          this.usersService.findByIdPassAndUser(this.userData).subscribe(result => {
            if (result.length > 0) {
              this.user = result[0];

            } else {
              this._router.navigate(['/register']);
            }
          });
        }
      }
    );
  }



  get f() { return this.registerForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }

    this.user.password = sha256(this.password);
    this.usersService.updatePass(this.user).subscribe(result => {

      this.toastr.success('Password changed successfully.');
      this._router.navigate(['/register']);

    },
      (error) => {
        console.log("Error: " + error);
        this.toastr.warning('Service not available');
      });

  }

  onReset() {
    this.submitted = false;
    this.registerForm.reset();
  }

}
