import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgbModal, ModalDismissReasons, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { PouchDBService } from '../../services/pouchdb.service';
import { ToastrService } from 'ngx-toastr';
import { sha256 } from 'js-sha256';
import { environment } from '../../../environments/environment';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { EmailService } from '../../services/email.service';
import { UsersService } from 'src/app/services/users.service';

declare var require: any
const FileSaver = require('file-saver');
@Component({
  selector: 'app-download',
  templateUrl: './download.component.html',
  styleUrls: ['./download.component.css']
})
export class DownloadComponent implements OnInit {
  flagdownload;

  linkURL ='';
  urlLinux = environment.DOWNLOAD_LINUX;
  urlWin= environment.DOWNLOAD_WINDOWS;
  urlWinES=  environment.DOWNLOAD_WINDOWSES;

  data: any = {};
  descargaValida: boolean;
  modalOptions: NgbModalOptions;
  closeResult: string;
  usuario: any = {};
  registerForm: FormGroup;
  emailForm: FormGroup;
  submitted = false;
  submittedEmail = false;
  localItem: any = {};
  user: any = {};
  regexcorreo = new RegExp("^[_a-z0-9-]+(.[_a-z0-9-]+)*@[a-z0-9-]+(.[a-z0-9-]+)*(.[a-z]{2,4})$");
  flagPassword = false;
  email = '';
  idiomaSeleccionado:any;

  sistema:string = '';
  flagdescargar:boolean = true;



  constructor(public translate: TranslateService,
    private modalService: NgbModal,
    public pouchDBService: PouchDBService,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private toastr: ToastrService,
    private emailService: EmailService,    
    private usersService: UsersService,
    public router: Router) {
    if (localStorage.getItem('idiomaSeleccionado') !== null) {
      translate.addLangs(['en', 'es']);
      let idioma = localStorage.getItem('idiomaSeleccionado');
      translate.currentLang = idioma;
      translate.setDefaultLang(idioma);
    } else {
      translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en');
    }
  }

  ngOnInit(): void {

    this.registerForm = this.formBuilder.group({
      username: ['', [Validators.required]],
      password: ['', [Validators.required]]
    });
    this.emailForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email, Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
    });



    this.sistema = this.getOS();
    console.log("sistema ", this.sistema);
    if(this.sistema=="Mac OS" || this.sistema=="iOS"||this.sistema==="Android") {
      this.flagdescargar = false;
      console.log('Es Ipad Es Ipad Es Ipad ...  ');
    }

  }


  open(content, value,linkSelect) {
    if(localStorage.getItem('idiomaSeleccionado')==='es'){
      this.linkURL = this.urlWinES;
    }else{
      this.linkURL = this.urlWin;
    }
    console.log(this.linkURL);
    this.flagdownload = value;
    if (!this.localItem._id) {
      this.modalService.open(content, this.modalOptions).result.then((result) => {
        this.closeResult = `Closed with: ${result}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    } else {
      this.downloadFile();
    }
  }


  openAlert() {
    this.toastr.error(this.translate.instant('alerts.sistemanocompatible'));
  }


  /* funcion para detectar acciones con teclado  */
  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  cancel() {
    this.usuario = {
      "_id":"",
      "pass":""
    };
    this.submitted = false;
    this.submittedEmail = false;
    this.modalService.dismissAll();
    this.registerForm.reset();
  }


  // convenience getter for easy access to form fields
  get f() { return this.registerForm.controls; }
  get p() { return this.emailForm.controls; }


  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }
    let password = sha256(this.usuario.pass);
    let user = '';
    if (this.regexcorreo.test(this.usuario._id)) {
      user = this.usuario._id.toUpperCase();

    } else {
      user = this.usuario._id;
    }
    this.user = {
      id: user,
      password: password
    }
    
    this.usersService.login(this.user).subscribe((result) => {

      if (result.codigo === '003') {
        this.toastr.error(this.translate.instant('alerts.usernamenotexist'));
        return;
      } else if (result.codigo === '002') {
        this.toastr.error(this.translate.instant('alerts.wrongpass'));
        return;
      } else {
        this.authService.genericJWT(result, 'token');
        this.localItem._id = result['_id'];
        this.cancel();
        if (!this.flagdownload) {
          this.downloadFile();
        }
      }
    },
    (error) => {
      this.toastr.error(this.translate.instant('alerts.errorgeneral'));
              console.log('error en login: ' + error)
              return;
    });
  }

  sendEmail() {
    this.submittedEmail = true;
    // stop here if form is invalid
    if (this.emailForm.invalid) {
      return;
    } 
    
  
    this.usersService.findByEmail( this.email).subscribe(result => {
      if (result.length > 0) {
        var idPass = this.create_UUID();
        this.user = {
          idPass: idPass,
          name: result[0].id
        }
          this.usersService.updateIdPass(this.user).subscribe(res => {
            let bodyEmail = {
              "subject": "VentSupport - Change Password",
              "from": environment.EMAIL_FROM,
              "to": result[0].email.toLowerCase(),
              "html": this.generatehtmlChangePassword(result[0].id, idPass)
            };
            this.emailService.sendGenericEmail(bodyEmail).subscribe((resemail) => {
              this.toastr.info(this.translate.instant('alerts.emailsentto') + bodyEmail.to);
            },
              (error) => {
                this.toastr.error(this.translate.instant('alerts.errorgeneral'));
                console.log('error al enviar correo: ' + error);
              });
          });

        } else {
          this.toastr.error(this.translate.instant('alerts.unregisteredemail'));
        }
      
    });
  }

  

  public change() {
    this.flagPassword = !this.flagPassword;
  }



  downloadFile() {
    const pdfName = 'your_pdf_file';
    FileSaver.saveAs(this.linkURL, pdfName);
    this.localItem._id ='';
    this.cancel();

  }

  close() {
    this.authService.clear();
    this.localItem = this.authService.decode();
  }

  guardarIdioma(idioma) {
    this.idiomaSeleccionado=idioma;
    console.log(this.idiomaSeleccionado);
    localStorage.removeItem('idiomaSeleccionado');
    localStorage.setItem('idiomaSeleccionado', idioma);

  }
  create_UUID() {
    var dt = new Date().getTime();
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      var r = (dt + Math.random() * 16) % 16 | 0;
      dt = Math.floor(dt / 16);
      return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
    return uuid.toString();
  }

  public generatehtmlChangePassword(user, id) {
    let urlpass = environment.LINK + 'change-password?username=' + user + '&id=' + id;
    let html = "<div style='text-align: center;'>" +
      "<img src='https://aeolus01e4ce.blob.core.windows.net/demo02/logo_VentSupport.ico' style='width: 7%;' >" +
      "<br>" +
      "<h3>A password change was requested, if you do not request it, ignore this email.</h3><br>" +
      "<a style='padding: 10px 20px 10px 20px; background-color: #17a2b8; border-radius: 5px;color: white; font-size: 14px; font-weight: bold;' href='" + urlpass + "'  target='_blank'>Change password</a>" +
      "</div>";

      return html;
  }

  omit_special_char(event, type) {
    var regex;

    switch (type) {
      case 1:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        break;
      case 2:
        regex = new RegExp("^[a-zA-Z\ \]+$");
        break;
      case 3:
        regex = new RegExp("^[a-zA-Z0-9\.\_\@\-\]+$");
        break;
      default:
        regex = new RegExp("^[a-zA-Z0-9]+$");
        break;
    }

    var key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
    if (!regex.test(key)) {
       event.preventDefault();
       return false;
    } 
  }

  getOS() {
    let userAgent = window.navigator.userAgent,
        platform = window.navigator.platform,
        macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
        windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
        iosPlatforms = ['iPhone', 'iPad', 'iPod'],
        os = null;
  
        if (macosPlatforms.indexOf(platform) !== -1) {
          os = 'Mac OS';
        } else if (iosPlatforms.indexOf(platform) !== -1) {
          os = 'iOS';
        } else if (windowsPlatforms.indexOf(platform) !== -1) {
          os = 'Windows';
        } else if (/Android/.test(userAgent)) {
          os = 'Android';
        } else if (!os && /Linux/.test(platform)) {
          os = 'Linux';
        }
      
    return os;
  }


}