import { Params , ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { PouchDBService } from '../services/pouchdb.service';
import { EmailService } from '../services/email.service';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { ChartData } from '../chart/chart.data';
import { AuthService } from '../services/auth.service';


@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  username;
  flag =0;
  email;
  idioma='';

  constructor(
    public route: ActivatedRoute,
    public pouchDBService: PouchDBService,
    private _router: Router,
    public emailService: EmailService,
    private toastr: ToastrService,
    public translate: TranslateService,
    public chartData: ChartData,
    public authService: AuthService
    ) { 
      translate.addLangs(['en', 'es']);
      translate.setDefaultLang('en'); 
    }

  ngOnInit(): void {

this.route.params.subscribe(
  (params: Params) => {
    this.username = params.username;
    if(this.username != undefined){
      this.activeUser();
    }else{
      this._router.navigate(['/register']);

    }
  }
);
    this.idioma = localStorage.getItem('idiomaSeleccionado');

  }

  public activeUser(){
    this.pouchDBService.activeUser(this.username).subscribe(result => {
      if(result._id){
        this.email=result.email;
        this.authService.genericJWT(result, 'jwtkey');
        this.flag = 200;
        let emailBody = this.chartData.htmlCredential(this.idioma, this.email.toLowerCase());
        this.emailService.sendCredential(emailBody).subscribe((res) => {
          this.toastr.success('Mail sent to: '+result.email);
        },
          (error) => {
            console.log("error email: "+JSON.stringify(error));
            this.toastr.warning('Error sending key to email: ' +result.email);
          });
      }else{
        this.flag = 404;
      }
   },error => {
    if(error.status == "404") {
      this.toastr.info('Username does not exist.');

   return error;
 } else {
  this.toastr.info('An attempt to activate a user failed.');
     return new Promise((resolve, reject) => {
         reject(error);
     });
  } 
  });
}
}
